.container {
	display: grid;
	grid-template-columns: 7fr 3fr;
}

.myId {
	margin-right: 5rem;
	border-radius: 5px;
	background: #c9d6ff; /* fallback for old browsers */
	background: -webkit-linear-gradient(to right, #e2e2e2, #c9d6ff); /* Chrome 10-25, Safari 5.1-6 */
	background: linear-gradient(
		to right,
		#ffffff,
		rgb(252, 251, 255)
	); /* W3C, IE 10+/ Edge, Firefox 16+, Chrome 26+, Opera 12+, Safari 7+ */

	padding: 2rem;
	display: grid;
	justify-content: center;
	align-content: center;
	box-shadow: 0px 6px 20px #1f2635;
}

.call-button {
	text-align: center;
	margin-top: 2rem;
	color: black;
}

.video-container {
	display: grid;
	grid-template-columns: 1fr 1fr;
	justify-content: center;
	align-content: center;
	margin-top: 10rem;
	margin-left: 10rem;
}

.video-container video{
	border-radius: 5px;
	border-style: solid;
  	border-width: 5px;
	border-color: rgb(7, 36, 54);

}

.caller {
	text-align: center;
	color: rgb(80, 75, 75);
}

body {
	background: #4c91e0; /* fallback for old browsers */
	background: -webkit-linear-gradient(to right, #8e54e9, #4776e6); /* Chrome 10-25, Safari 5.1-6 */
	background: linear-gradient(
		to left,
		#ecb3ea,
		#85e0f7
		
	); /* W3C, IE 10+/ Edge, Firefox 16+, Chrome 26+, Opera 12+, Safari 7+ */
}

.button-video {
	position: relative;
	color: rgb(255, 250, 250);
	margin-left: 500px;
	padding: 16px 32px;
	border-radius: 999px;
	background-color: rgb(31, 63, 168);
	font-weight: 700;
	text-transform: uppercase;
	font-size: 18px;
	margin-top: 16px;
	text-align: center;
	cursor: pointer;
	border: none;
	box-shadow: px 3px 10px var(--dark);
  }
  